html { font-family: "Arial", "Roboto", sans-serif; }
pre, code { font-family: "Roboto Mono", "Cascadia Code", "Consolas", "Lucida Console", monospace; }

.panel { @extend .cute-border; overflow: hidden; }

.web-button { width: 88px; height: 31px; }
.web-button--small { width: 80px; height: 15px; }

#fediring img,
.image-heading img,
.web-button, .web-button--small {
 image-rendering: crisp-edges;
 image-rendering: -webkit-optimize-contrast;
}

.cute-border {
 border-width: 3px;
 border-style: double;
 border-radius: 4px;
}

td { padding: 0; }
ul { padding-left: 10px; }

#mobile-nav { display: none; }

.layout__content {
 table { width: 100%; font-size: inherit; }
 img { max-width: 100%; }

 table, td, th {
 border-collapse: collapse;
 border-width: 1px;
 border-style: solid;
 }
 
 td, th {
 padding: 6px;
 }
}

.two-columns {
 -webkit-column-count: 2;
 -webkit-column-gap: 5em;
 -moz-column-count: 2;
 -moz-column-gap: 5em;
 column-count: 2;
 column-gap: 5em;
}

table.two-columns td {
 width: 50%;
}

.fill-x, .blog-post-list {
 width: 100%;
}

h1 { text-align: center; }
h1, h2, h3, h4, h5, h6 {
 &:target {
 margin: -1px;
 padding: 1px;
 border-width: 1px;
 border-style: dotted;
 border-radius: 4px;
 }
}
aside { float:right; }

.light-text { text-align: center; }

#kyouko {
 position: fixed;
 position: absolute;
 z-index: -1;
 bottom: 0;
 right: 0;
}

#banner { width: 100%; }

hr {
 border: none;
 border-top-width: 1px;
 border-top-style: solid;
}

a img { border: none; } 

.warning {
 font-weight: bold;
 border-width: 1px;
 border-style: solid;
 padding: 0 4px 4px 4px;
 img { position: relative; top: 4px; }
}

.small-wide-heading {
 font-weight: bold;
 text-align: left;
 font-size: inherit;
}
.large-table {
 min-width: 100%;
 width: unset;
 overflow-x: auto;

 th, td {
 padding: 2px 4px !important;
 }
}

table.image-table img { width: 100%; }

.profile-button {
 width: 24px;
 height: 24px;
}

summary {
 font-weight: bold;
 cursor: pointer;
}

details[open] > summary { margin-bottom: 1em; }

.article {
 margin: 2em;
 // font-size: 1.5em;

 p:nth-child(1) { margin-top: 0; }

 h2, h3, h4, h5, h6 { text-decoration: underline; margin-top: 3em; }

 // code { line-height: $line-height * 0.8; }
 code, pre { margin: 0; }
 .highlight { 
 line-height: normal;
 background: lightblue;
 padding: 4px;
 margin-right: 12px;
 border-radius: 8px;
 overflow-x: auto;
 }

 img { width: 100%; border: black 1px solid; }
}

.a11y-link {
 position: absolute;
 top: 0px;
 left: 0px;
 opacity: 0;

 &:focus {
 opacity: 1;
 border: 2px white solid;
 background: blue;
 color: white;
 padding: 8px;
 }
}

ul.invisible {
 padding: 0;
 li { display: inherit; }
}

table.invisible {
 &, td, th {
 border-collapse: collapse;
 border-width: 0;
 border-style: none;
 }
}

.scroll-x { overflow-x: auto; }

.services-table {
 td > a > img {
 min-width: 16px;
 max-width: unset;
 min-height: 16px;
 }
}

.switcher { 
 .selected {
 font-weight: bold;
 }
}

#imood {
 top: 3px;
 position: relative;
 border: none;
 height: 15px;
}

#fedi-slot {
 vertical-align: top;
}

.bios {
 background: black;
 color: #A8A8A8;
 font-family: "437 IBM VGA 8x16", "Px437 IBM VGA 8x16", "System", monospace;
 font-size: 16px;
 font-smooth: never;
 -webkit-font-smoothing : none;
 // HACK: Why the fuck do I have do manually adjust sub-pixel?
 padding: 8px 8px 8px 8.4px;
}

.fediverse-post {
 &__avatar {
 // Who would've known that cascading styles actually
 // sucked if you constantly had to override shit because
 // you don't want to explicitly define your fucking
 // classes in each element?
 max-width: none !important;
 width: 32px;
 height: 32px;
 border: 1px solid black;
 }
 &__attachment {
 max-height: 96px;
 max-width: 100%;
 }
 &__author {
 font-weight: bold;
 margin-top: 0;
 }
 code { white-space: break-spaces; }
}